@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");
/* RESET CSS */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 100;
}

nav ul {
  width: 50%;
  display: flex;
  margin: 0 auto;
  justify-content: space-evenly;
  align-items: center;
}
nav ul li a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  font-size: 24px;
  line-height: 70px;
  text-shadow: 0 0 20px rgba(0, 0, 0, 0.336);
  position: relative;
}
nav ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: black;
  display: block;
  bottom: -8px;
  transition: 0.4s ease-in-out;
  opacity: 0;
}
nav ul li a:hover::after {
  opacity: 1;
}

@keyframes visualMove {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.07);
  }
}
.visual {
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.visual img {
  display: block;
  width: 100%;
  animation: visualMove 4s forwards;
}
.visual h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  line-height: 1.2;
  font-weight: 900;
  font-size: 70px;
  color: rgb(255, 255, 255);
  text-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes leftFadeIn {
  from {
    opacity: 0;
    left: -100%;
  }
  to {
    opacity: 1;
    left: 0;
  }
}
@keyframes slideUp {
  from {
    opacity: 0;
    top: 5vh;
  }
  to {
    opacity: 1;
    top: 0;
  }
}
.profile {
  display: flex;
  align-items: center;
  height: 100vh;
  overflow-y: hidden;
}
.profile img {
  position: relative;
  display: block;
  width: 100%;
  animation: leftFadeIn 1s forwards ease-in-out;
}
.profile .profileImg {
  width: 50%;
}
.profile .profileDesc {
  width: 45%;
  margin: 0 auto;
}
.profile .profileDesc h2 {
  position: relative;
  font-size: 50px;
  font-weight: bold;
  animation: 1s ease-in-out slideUp forwards;
}
.profile .profileDesc h2 span {
  font-weight: 300;
  font-size: 30px;
}
.profile .profileDesc > div:first-of-type ul {
  animation: 1s ease-in-out slideUp forwards;
  animation-delay: 0.3s;
  opacity: 0;
}
.profile .profileDesc > div:last-of-type ul {
  animation: 1s ease-in-out slideUp forwards;
  animation-delay: 0.7s;
  opacity: 0;
}
.profile .profileDesc h3 {
  position: relative;
  font-size: 30px;
  font-weight: bold;
  animation: 1s ease-in-out slideUp forwards;
  animation-delay: 0.5s;
  opacity: 0;
}
.profile .profileDesc ul {
  position: relative;
  margin: 15px 0 40px;
}
.profile .profileDesc li {
  display: flex;
  line-height: 2;
  font-size: 24px;
}
.profile .profileDesc li span {
  width: 20%;
  font-weight: 500;
  color: #333;
}

@keyframes filmoSlide {
  from {
    top: 0;
  }
  to {
    top: -100vh;
  }
}
.filmo {
  position: relative;
  animation: filmoSlide 1s ease-in-out forwards;
  animation-delay: 0.7s;
}

.filmoBg {
  height: 100vh;
  background: url("../public/images/01-0360.jpg");
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}
.filmoBg h1 {
  font-size: 150px;
  font-weight: 900;
  font-style: italic;
  color: white;
}

.filmoDesc .drama {
  width: 80%;
  margin: 100px auto;
  padding-bottom: 100px;
  position: relative;
}
.filmoDesc h2 {
  position: absolute;
  top: -30px;
  left: -30px;
  font-size: 130px;
  font-weight: 900;
  font-style: italic;
  margin-bottom: 30px;
  z-index: -1;
}
.filmoDesc ul {
  padding: 70px 0;
  display: flex;
  justify-content: space-between;
}
.filmoDesc li {
  height: 450px;
  text-align: center;
}
.filmoDesc li p:first-of-type {
  font-weight: bold;
  font-size: 30px;
  margin: 20px 0 10px;
}
.filmoDesc li p:first-of-type span {
  font-weight: 300;
}
.filmoDesc li p:last-of-type {
  font-size: 20px;
}
.filmoDesc li img {
  display: block;
  height: 100%;
}

